@use "variables" as v;

//Mixins for mediaQueries
@mixin phone{
    @media (min-width: v.$phone){
        @content;
    }
}
@mixin tablet{
    @media (min-width: v.$tablet){

        @content;
    }
}
@mixin desktop{
    @media (min-width: v.$desktop){
        @content;
    }
}

//General Mixins
@mixin grid($columns, $rows, $gap){
    display: grid;
    grid-template-columns: repeat($columns, 1fr);
    grid-template-rows: repeat($rows, 1fr);
    gap: $gap;
}

@mixin button($bg, $bg-hovers, $txt, $txt-hover){
    background-color: $bg;
    color: $txt;
    transition: v.$tran-btn;
    padding: 1rem 1rem;
    font-weight: bold;
    text-decoration: none;
    //text-transform: uppercase;
    font-size: 1.6rem;
    letter-spacing: 0.1rem;
    border: none;
    border-radius: 1rem;
    margin-top: 2rem;
    margin-bottom: 2rem;
    width: 100%;
    padding: 1rem 4rem;
    @include tablet{
        width: auto;
    }
    &:hover{
        background-color: $bg-hovers;
        color: $txt-hover;
        transition: v.$tran-btn;
    }
}