@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.content {
    .__Tabla {
        .tabla {
            .tabla__cabecera{
                tr{
                    .cabecera{
                        width: fit-content;
                        padding: 1rem;
                        text-align: center;
                    }
                }
            }
        }
    }
}

//Para centrar todo
.content {
    .__Tabla {
        .tabla {
            .tabla__cabecera,
            .tabla__cuerpo {
                tr {
                    .id,
                    .nombre,
                    .email,
                    .tipoU {
                        text-align: center;
                        vertical-align: middle;
                    }
                }
            }
        }
    }
}

//Botn crear usuario
.content {
    .crearU {
        @include m.button(v.$secundario, v.$secundario-hover, #111111, #FFFFFF);
    }
}
