@use 'Base/variables' as v;
@use 'Base/mixins' as m;

body{
    background-color: v.$bg-color;
}
//Ajustando tamao de cabeceras a contenido
.content {
    .__Tabla {
        .tabla {
            .tabla__cabecera{
                tr{
                    .cabecera{
                        width: fit-content;
                        padding: 1rem;
                        text-align: center;
                        vertical-align: middle;
                    }
                }
            }
        }
    }
}


//Para centrar todo
.content {
    .__Tabla {
        .tabla {
            .tabla__cuerpo {
                tr {
                    .id,
                    .nombreSala,
                    .estado,
                    .cupoMax,
                    .numeroS,
                    .pisoS {
                        text-align: center;
                        vertical-align: middle;
                        padding: 1rem 4rem 1rem 4rem;
                    }
                }
            }
        }
    }
}

//Ajustando especficamente cabecera de Cupo Mximo
.content {
    .__Tabla {
        .tabla {
            .tabla__cabecera{
                tr{
                    #CupoMax{
                        width: 23rem;
                    }
                }
            }
        }
    }
}


//Botn crear sala
.content {
     h1{
        color:v.$txt-azul;
     }
    .crearS {
        @include m.button(v.$secundario, v.$secundario-hover, v.$negro, v.$blanco);
    }
}

