@use 'Base/variables' as v;

.content{
    padding:  5rem;
}

body{
    background-color: v.$bg-color;
}

table {
    width: fit-content;
    max-width: 100%;
}

//Responsive
@media (min-width: 769px) {
    table {
        width: 100%;
    }
}

@media (max-width: 950px) {
    .title-example{
        font-size: 3.2rem ;
    }
    table {
        font-size: 1.2rem;
    }
}

@media (max-width: 768px) {
    .title-example{
        font-size: 2.5rem ;
    }
    table {
        font-size: 1rem;
        font-weight: bold;
    }
}


//Hover de la tabla
.table-hover tbody tr:hover td,
.table-hover tbody tr:hover th {
    background-color: v.$table-hover;
}

//Diseo de botones
.dt-paging-button {
    padding: 0.5rem !important;
    border: none !important;
}

table tr th {
    
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

//Color de la cabecera de la tabla
.content{
    .__Tabla{
        .tabla{
            .tabla__cabecera{
                .cabecera{
                    background-color: v.$tabla-cabecera;
                    color: v.$tabla-cabecera-txt;
                }
            }
        }
    }
}
.content{
    .__Tabla{
        .tabla{
            .tabla__cabecera{
                .cabecera:hover{
                    background-color: v.$tabla-cabecera-hover;
                }
            }
        }
    }
}

//Botones acciones
.acciones {
    text-align: center;
}

.tabla .btn {
    display: inline-flex;
}

.tabla .btn img {
    width: 1.8rem;
    height: 1.8rem;
}

