@use 'Base/variables' as v;
@use 'Base/mixins' as m;

    .container-shadow {
        padding: 20px;
        background-color: #ffffff;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
        border-radius: 8px;
        margin: 20px;
        height: auto;
        overflow-y: auto;

        @media (min-width: 768px) {
            max-width: calc(100% - 40px);
            margin-top: 20px;
            margin-bottom: 20px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
    }

    .editForm {
        text-align: center;
        background-color: v.$blanco;

        @include m.tablet {
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 90%;
            text-align: left;
        }

        .editForm__titulo {
            color: v.$negro;
            font: v.$titulos;
            margin: 0;
            font-size: v.$titulos-size;
            //top: 2rem;
            text-align: center;

            @include m.tablet {
                font-size: 4rem;
                max-width: calc(100% - 40px);

            }
        }

        .editForm__label {
            font-size: 1.6rem;
            margin-bottom: 5px;

            @include m.tablet {
                font-size: 2rem;
                max-width: calc(100% - 40px);

            }
        }

        .inputForm {
            border: 1.5px solid #ecedec;
            border-radius: 10px;
            height: 50px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding-left: 10px;
            transition: 0.2s ease-in-out;
            margin-bottom: 2rem;
            font-size: 1.4rem;
            @include m.tablet{
                max-width: 60rem;
                max-width: calc(100% - 40px);

            }
            .view-icon{
                cursor: pointer;
                margin-right: 2rem;
            }
            .input {
              margin-left: 10px;
              border-radius: 10px;
              border: none;
              width: 85%;
              height: 100%;
            }
            .input:focus {
              outline: none;
            }
            
            &:focus-within {
              border: 1.5px solid v.$primario;
            }
        }

        .button-container {

            @include m.tablet {
                display: flex;
                //flex-direction: row-reverse;
                gap: 2rem;
                max-width: 50rem;
                max-width: calc(100% - 40px);

            }

            .button {
                @include m.button(v.$secundario, v.$secundario-hover, v.$negro, v.$blanco);
            }
            
            .button-submit {
                @include m.button(v.$primario, v.$primario-hover, v.$blanco, v.$negro);
            }
        }
    }